import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class EventService {

  private domainUrl = "http://localhost:3000/";

  private _eventsUrl =  this.domainUrl + "api/events";
  private _specialEventsUrl = this.domainUrl + "api/special";
  
  constructor(private http: HttpClient) { }
  
  getEvents() {
    return this.http.get<any>(this._eventsUrl)
  }

  getEventById(id) {
    return this.http.get<any>(this._eventsUrl);
  }

  getSpecialEvents() {
    return this.http.get<any>(this._specialEventsUrl)
  }

  getSpecialEventById(id) {
    return this.http.get<any>(this._specialEventsUrl + '/' + id);
  }
}



